import React from "react";
import classnames from "classnames";
import { Redirect } from "react-router-dom";
import BackButton from "../Elements/BackButton";
import { overviewBoth, inviteStartup, viewCompanies } from "../../../routes";
import {
  container,
  small_container,
  center_container,
  inner_container
} from "../../Style.module.css";

import { Mutation } from "react-apollo";
import { createOrganization } from "../../../Apollo/Mutations";

class NewCompanyComp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      v: "",
      redirect: false,
      position: false
    };
  }

  componentDidMount() {
    if (navigator.geolocation && !this.state.position) {
      navigator.geolocation.getCurrentPosition(pos => {
        let position = {
          latitude: pos.coords.latitude,
          longitude: pos.coords.longitude
        };

        this.setState({ position });
      });
    }
  }

  render() {
    let { mutate, data, loading, error } = this.props;

    if (!error && !loading && data) {
      let { id } = data.createOrganization;
      return <Redirect to={`${overviewBoth}?id=${id}`} />;
    }

    return (
      <content>
        <div
          className={classnames(container, small_container, center_container)}
        >
          <div className={inner_container}>
            <form
              onSubmit={e => {
                e.preventDefault();
                if (loading) return;

                if (this.state.v.length <= 2) return;
                let input = { name: this.state.v };
                if (this.state.position) {
                  input.position = this.state.position;
                }
                mutate({ variables: { input } });
              }}
            >
              <div style={{ marginBottom: "20px" }}>
                <label>
                  Company name
                  <input
                    type="text"
                    value={this.state.v}
                    placeholder="Dollar Press Inc."
                    onChange={e => {
                      this.setState({
                        v: e.target.value
                      });
                    }}
                  />
                </label>
              </div>
              <div>
                <input
                  type="submit"
                  value="Continue"
                  style={{
                    visibility: this.state.v.length >= 3 ? "visible" : "hidden"
                  }}
                />
                {loading && <i className="fa fa-spinner fa-spin" />}
              </div>
            </form>
            <BackButton link={viewCompanies} />
          </div>
        </div>
      </content>
    );
  }
}

export const NewCompany = () => (
  <Mutation mutation={createOrganization}>
    {(mutate, { data, loading, error }) => (
      <NewCompanyComp
        mutate={mutate}
        data={data}
        loading={loading}
        error={error}
      />
    )}
  </Mutation>
);
