import React from "react";
import { Query } from "react-apollo";
import { pageScraper } from "../../../Apollo/Queries";

export class PageScraper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      val: "",
      skip: true
    };
  }

  render() {
    return (
      <content>
        <Query
          query={pageScraper}
          variables={{ url: this.state.val }}
          skip={this.state.skip}
        >
          {({ loading, data, error }) => {
            return (
              <div>
                <form
                  onSubmit={e => {
                    e.preventDefault();
                    this.setState({ skip: false });
                  }}
                >
                  <input
                    type="text"
                    value={this.state.val}
                    placeholder="url"
                    disable
                    onChange={e => this.setState({ val: e.target.value })}
                    onFocus={() => this.setState({ skip: true })}
                    style={{ opacity: loading && !this.state.skip ? 0.4 : 1 }}
                    disabled={loading && !this.state.skip}
                  />
                  <div style={{ marginTop: "10px" }}>
                    <input
                      type="submit"
                      value="Scrape..."
                      style={{
                        visibility:
                          this.state.val.length >= 3 ? "visible" : "hidden"
                      }}
                    />
                    {loading && !this.state.skip && (
                      <i className="fa fa-spinner fa-spin" />
                    )}
                  </div>
                </form>
                {data && !loading && <pre>{JSON.stringify(data, null, 2)}</pre>}
              </div>
            );
          }}
        </Query>
      </content>
    );
  }
}
