export const login = "/login";
export const forgotPassword = "/forgotPassword";
export const signup = "/signup";
export const awaiting = "/awaiting";
export const flash = `/flash/:id`;
export const link_bridge = `/link`;

export const Routes = () => (
  <Router basename="/">
    <ScrollToTop>
      <div className={container}>
        <Header />
        <div className={content}>
          <Switch>
            <Route path="/cb" component={Cb} />
            <Route path={flash} component={Flash} />
            <Route path={link_bridge} component={LinkBridge} />

            <Route path={publicSubmitForm} component={PublicSubmitForm} />

            <Route
              exact
              path={public_view_evaluation}
              component={PublicViewEvaluation}
            />
            <Route
              path={public_new_company}
              component={PublicNewCompanyRouter}
            />
            <Route exact path={signOut} component={SignOut} />
            <Route exact path={frontpage} component={FrontPage} />

            <Route exact path={aboutpage} component={AboutPage} />

            <Route exact path={signup} component={Signup} />
            <Route exact path={login} component={Login} />
            <Route exact path={forgotPassword} component={ForgotPassword} />
            <Route exact path={awaiting} component={Awaiting} />
            <Route path={dashboard} component={LoggedInPage} />
            <Route render={() => <Redirect to={frontpage} />} />
          </Switch>
        </div>
        <Switch>
          <FooterSection />
        </Switch>
      </div>
    </ScrollToTop>
  </Router>
);
