import React from "react";
import { Redirect } from "react-router-dom";
import qp from "../../../utils/queryParams";
import { Query } from "react-apollo";
import { getEvaluation } from "../../../Apollo/Queries";
import BigButton from "../Elements/BigButton";
import { buttonsContainer } from "./OverviewBoth.module.css";
import classnames from "classnames";
import { page_title } from "../Elements/Grid.module.css";
import {
  dashboard,
  infoOverview,
  evaluation_new,
  evaluationOverview
} from "../../../routes";

import BackButton from "../Elements/BackButton";
import { GhostLoader } from "../Elements/GhostLoader";
import { color1, color3_bg_h, color4_bg_h } from "../../Colors.module.css";

export class OverviewBoth extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: null,
      redirect: false
    };
  }

  componentDidMount() {
    let { id } = qp(this.props.location.search);
    this.setState({ id: id[0] });
  }

  render() {
    let { history } = this.props;
    if (!this.state.id) return <span />;

    if (this.state.redirect) {
      return <Redirect to={`${this.state.redirect}?id=${this.state.id}`} />;
    }

    return (
      <Query
        query={getEvaluation}
        variables={{ orgId: this.state.id }}
        fetchPolicy="cache-and-network"
      >
        {({ data, error, loading }) => {
          console.log("{ data, error, loading }", { data, error, loading });

          let orgName = "";
          let orgId = "";
          let evaluationID = "";
          if (
            !error &&
            !loading &&
            data.getEvaluation &&
            data.getEvaluation.organization
          ) {
            orgName = data.getEvaluation.organization.name;
            orgId = data.getEvaluation.orgId;
            evaluationID = data.getEvaluation.id;
          }

          return (
            <content>
              {loading && <GhostLoader />}
              {!loading && (
                <div>
                  <div className={classnames(page_title, color1)}>
                    {orgName ? orgName : <span>&nbsp;</span>}
                  </div>

                  <div className={buttonsContainer}>
                    <BigButton
                      label="evaluate"
                      className={color3_bg_h}
                      link={`${evaluation_new}/${evaluationID}`}
                    />
                    <BigButton
                      label="hard facts"
                      className={color4_bg_h}
                      link={`${infoOverview}?id=${orgId}`}
                    />
                  </div>
                </div>
              )}
              <BackButton history={history} />
            </content>
          );
        }}
      </Query>
    );
  }
}
