import React from "react";
import classnames from 'classnames';
import { Redirect, Switch, withRouter } from "react-router-dom";

import {
  container,
  inner_container,
  section,
  section_title,
  question_wrapper,
  question_title,
  option_wrapper,
  option_title
} from './PublicSubmitForm.module.css'

import combined_data from "../PublicNewCompany/Pages/combinedData";


class Question extends React.Component {
  constructor(props) {
    super(props)
  }

  render() {

    let { question } = this.props;

    return (
      <div className={question_wrapper}>
        <div className={question_title}>
          {question.title}
        </div>

        {
          question.type === "radio" &&
          question.options &&
          question.options.map((option, i) => (
            <div>
              <label
                key={`radio-${i}`}
                >
                <input
                  type="radio"
                  value={option.val}
                />
                <span>
                  {option.text}
                </span>
              </label>
            </div>
          ))
        }


        {
          question.type === "check" &&
          question.options &&
          question.options.map((option, i) => (
            <div>
              <label
                key={`checkbox-${i}`}
                >
                <input
                  type="checkbox"
                  value={option.val}
                />
                <span>
                  {option.text}
                </span>
              </label>
            </div>
          ))
        }



        {
          // console.log('question', question)
        }

        {
          // question.options &&
          // question.options.map((option, i) => (
          //   <Option
          //     key={`${i}`}
          //     option={option}
          //   />
          // ))
        }
      </div>
    )

  }

}

class Option extends React.Component {
  constructor(props) {
    super(props)
  }

  render() {

    let { option } = this.props;

    return (
      <div className={option_wrapper}>
        <div className={option_title}>
          {option.text}
        </div>
      </div>
    )
  }

}




class Component extends React.Component {
  constructor(props) {
    super(props)
  }

  render() {
    return (
      <div className={container}>
        <div className={inner_container}>
          {
            combined_data.map(data => (
              <div
                key={`${data.title}`}
                className={section}
                >
                <div className={section_title}>
                  {data.title}
                </div>
                {
                  data.questions.map((question, i) => (
                    <Question
                      question={question}
                    />
                  ))
                }
              </div>
            ))
          }
        </div>
      </div>      
    )
  }
}

export default Component;




