export const LoggedInPageComponent = ({ location }) => {
  const [userLoggedIn, setUserLoggedIn] = useState(undefined);
  useEffect(() => {
    Auth.currentAuthenticatedUser()
      .then(() => setUserLoggedIn(true))
      .catch(() => setUserLoggedIn(false));
  }, []);

  if (userLoggedIn === false) {
    console.log("not logged in. Redirecting");
    return (
      <Redirect
        to={{
          pathname: signup,
          state: location
        }}
      />
    );
  }

  return (
    <main>
      <Switch>
        <Route exact path={profile} component={Profile} />
        <Route path={dashboard} component={DashboardRouter} />
      </Switch>
    </main>
  );

  // return (
  //   <Query query={getUser} fetchPolicy="cache-and-network">
  //     {({ data, loading, error }) => {
  //       if (loading) return <GhostLoader />
  //       if (!loading && !error && data) {
  //         let user = data.getUser || {};
  //         if (user.email === null) {
  //           return <Redirect to={profile} />;
  //         }
  //       }
  //       return (
  //         <main>
  //           <Switch>
  //             <Route exact path={profile} component={Profile} />
  //             <Route path={dashboard} component={DashboardRouter} />
  //           </Switch>
  //         </main>
  //       );
  //     }}
  //   </Query>
  // )
};

export const LoggedInPage = withRouter(LoggedInPageComponent);
