import React from "react";
import { Auth } from "aws-amplify";
import { Link, Redirect } from "react-router-dom";
import { Query } from "react-apollo";
import { getUser } from "../../Apollo/Queries";
import { profile, signOut, reporting, viewCompanies } from "../../routes";

import {
  container,
  left_side,
  right_side,
  title_class,
  email_class,
  icons
} from "./Header.module.css";

export class DashboardHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      openMenu: false
    };
  }

  render() {
    return (
      <Query query={getUser} fetchPolicy="cache-and-network">
        {({ data, loading, error }) => {
          let email;

          let have_profile;

          if (!loading && !error && data) {
            let user = data.getUser || {};
            have_profile = user.email !== null;
            // if (user.email === null) {
            //   return <Redirect to={profile} />;
            // }
            // email = user.email;
          }

          return (
            <div className={container}>
              <div className={left_side}>
                <div className={title_class}>notata.io</div>

                {have_profile && (
                  <div className={icons}>
                    <Link to={reporting}>
                      <i className="fal fa-chart-bar" />
                    </Link>

                    <Link to={viewCompanies}>
                      <i className="fal fa-list-alt" />
                    </Link>
                  </div>
                )}
              </div>

              <div className={right_side}>
                <div className={icons}>
                  {have_profile && (
                    <Link to={profile}>
                      <i className="fal fa-user-alt" />
                    </Link>
                  )}

                  <Link to={signOut}>
                    <i className="fal fa-sign-out-alt" />
                  </Link>
                </div>
              </div>
            </div>
          );
        }}
      </Query>
    );
  }
}
