import React from 'react';
import qp from '../utils/queryParams';

export class Cb extends React.Component {

  constructor(props) {
    super (props);
    this.state = {
      queryData: ''
    }
  }

  componentDidMount() {
    let queryData = qp(this.props.location.search);
    this.setState({queryData: JSON.stringify(queryData, null, 2)})
  }

  render() {
    return (
      <div style={{padding: '30px'}}>
        <pre>
          {this.state.queryData}
        </pre>
      </div>

    )
  }


}