const isEven = n => n % 2 == 0;

const Body = ({str}) => {

  let ss = str.split(/\{(.*?)\}/)
  if (ss.length === 1) return <div>{str}</div>;

  return (
    <div>
      {
        ss.map((s, i) => {
          if (isEven(i)) {
            return <span>{s}</span>
          }

          let pp = s.split('|').map(p => p.split('='));;
          let url = pp[0][1];
          let title = pp[1][1];
          return <a href={url} target="_blank" >{title}</a>
          
        })
      }
    </div>
  )


  // return <div>{str}</div>
}

class InputTextArea extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      placeholder: 'Hva har du på hjertet?',
      value: ''
    }
  }

  componentDidMount() {
    this.setState({val: this.props.val})
  }

  render() {
    let { val, save } = this.props;
    return (
      <textarea
        onChange={e => this.setState({val: e.target.value})}
        className={text_area}
        placeholder={this.state.placeholder}
        onFocus={() => this.setState({placeholder: ''})}
        onBlur={() => {
          this.setState({placeholder: 'Hva har du på hjertet?'})
          save(this.state.val)
        }}
        value={this.state.val}
      />
    )
  }
}

class Component extends React.Component {

  constructor(props) {
    super(props)
  }

  render() {

    let { d, id } = this.props;

    return (
      <Mutation
        mutation={updateFlash}
        >
        {(mutate, { data, loading, error }) => {

          return (
            <div className={container}>
              <div className={white_cover}/>
              <div className={content}>

                <div className={header}>
                  <div className={title_section}>
                    <i className="fas fa-bolt" />
                    <h1>{d.title}</h1>
                    <div className={customer_section}>
                      {d.subTitle}
                    </div>
                  </div>
                </div>

                {
                  d.sections.map((s, i) => (
                    <div className={section} key={`section-${i}`}>
                      <div className={sub_header}>
                        {s.preHeader}
                      </div>

                      <div className={eval_thumb}>
                        <i
                          className="fas fa-thumbs-up"
                          style={{opacity: s.evaluation === 'like' ? 1 : 0.2 }}
                          onClick={() => {
                            let variables = {id: id, input: {section: i, val: 'like'}}
                            let optimistic = cloneDeep(d);
                            optimistic.sections[i].evaluation = 'like';
                            mutate({
                              variables,
                              optimisticResponse: {
                                __typename: "Mutation",
                                updateFlash: {
                                  ...optimistic
                                }
                              }
                            })
                          }}
                        />
                        <i
                          className="fas fa-thumbs-down"
                          style={{opacity: s.evaluation === 'dislike' ? 1 : 0.2 }}
                          onClick={() => {
                            let variables = {id: id, input: {section: i, val: 'dislike'}}
                            let optimistic = cloneDeep(d);
                            optimistic.sections[i].evaluation = 'dislike';
                            mutate({
                              variables,
                              optimisticResponse: {
                                __typename: "Mutation",
                                updateFlash: {
                                  ...optimistic
                                }
                              }
                            })
                          }}
                        />
                      </div>

                      <h2>{s.header}</h2>
                      <div className={section_sub}>
                        <Body str={s.body}/>
                      </div>

                      {
                        s.talks && (
                          <Talks
                            dd={s.talks}
                          />
                        )
                      }                      

                      {
                        s.numberBalls && (
                          <NumberBalls
                            dd={s.numberBalls}
                          />
                        )
                      }

                      {
                        s.startups && (
                          <Startups
                            dd={s.startups}
                          />
                        )
                      }

                      {
                        s.calendar && (
                          <Calendar
                            dd={s.calendar}
                          />
                        )
                      }

                      {
                        s.image && (
                          <div className={image_div}>
                            <img src={s.image} />
                          </div>
                        )
                      }

                    </div>
                  ))
                }


                <div className={section}>
                  <div className={sub_header}>
                    Feedback
                  </div>
                  <h2>Hva vil du ha mer av?</h2>
                  <div className={section_sub}>
                    Er det noe du savner, eller noe du ønsker mer av? Eller har du andre kommentarer til denne flæsj-rapporten? Vi tar gjerne i mot tilbakemeldinger, slik at vi kan jobbe for å lage den perfekte flæsjen for deg!
                  </div>

                  <InputTextArea
                    val={d.freeInput || ''}
                    save={newVal => {
                      let variables = {id: id, input: {freeInput: newVal}}
                      // let optimistic = cloneDeep(d);
                      // optimistic.sections[i].evaluation = 'like';
                      mutate({
                        variables,
                        optimisticResponse: {
                          __typename: "Mutation",
                          updateFlash: {
                            ...d,
                            freeInput: newVal
                          }
                        }
                      })

                    }}
                  />

                </div>

              </div>

            </div>
          )

        }}
      </Mutation>      
    )



  }

};


class CompWithQuery extends React.Component {
  constructor(props) {
    super(props)
  }
  render () {
    let id = this.props.match.params.id;
    if (!id) return <div>No id...</div>
    return (
      <Query
        query={getFlash}
        variables={{ id }}
        fetchPolicy="cache-and-network"
      >
        {({ data, error, loading }) => {

          console.log('{ data, error, loading }', { data, error, loading })
          if (loading) return <GhostLoader />
          if (error) return <div>Error...</div>
          let d = data.getFlash;
          return <Component d={d} id={id} />
        }}
      </Query>   
    )
  }
}

export default withRouter(CompWithQuery);










