// *******************************
// * EDIT EXISTING TAG COMPONENT *
// *******************************
class EditTag extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: this.props.tag.name,
      submitting: false,
      success: false
    };
  }

  render() {
    let { tag, setLoading } = this.props;
    let { groupId, index, name, tag_type } = tag;

    return (
      <Mutation mutation={updateTags}>
        {(mutate, { data, loading, error }) => {
          if (!loading && !error && data && this.state.submitting) {
            this.setState({ submitting: false, success: true });
            this.props.setVal(data.updateTags[0]);
          }
          return (
            <div>
              {this.state.success && (
                <div className={success_save}>Successfully saved</div>
              )}
              <div className={tag_input}>
                <label>
                  <input
                    type="text"
                    value={this.state.value}
                    onChange={e =>
                      this.setState({ value: e.target.value, success: false })
                    }
                    placeholder="Cannot be empty"
                  />
                  {!this.state.submitting && (
                    <i
                      style={{
                        visibility:
                          this.state.value === "" ? "hidden" : "visible"
                      }}
                      className="fas fa-plus-circle"
                      onClick={() => {
                        if (this.state.submitting) return;
                        let variables = {
                          input: [
                            {
                              ...omit(tag, [
                                "createdBy",
                                "createdAt",
                                "updatedAt",
                                "__typename"
                              ]),
                              name: this.state.value
                            }
                          ]
                        };
                        mutate({ variables });
                        this.setState({ submitting: true });
                      }}
                    />
                  )}

                  {this.state.submitting && (
                    <i className="fas fa-spinner fa-spin" />
                  )}
                </label>
              </div>
            </div>
          );
        }}
      </Mutation>
    );
  }
}

// *******************************
// * MODAL TO EDIT OR DELETE TAG *
// *******************************
class Modal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      deleting: false
    };
  }

  render() {
    let { tag, show, close, updatedTag, deletedTag } = this.props;

    if (!show || !tag) return <span />;

    return (
      <div className={modal_class}>
        <div className={modal_class_inner}>
          <div className={modal_container}>
            <div className={close_modal} onClick={() => close()}>
              Close
            </div>

            <EditTag
              index={tag.index}
              groupId={tag.group || "no_group"}
              tag={tag}
              setVal={nVal => {
                updatedTag(nVal);
                this.setState({ loading: false });
              }}
              setLoading={() => {
                this.setState({ loading: true });
              }}
            />

            <Mutation mutation={deleteTag}>
              {(mutate, { data, loading, error }) => {
                if (!loading && !error && data && this.state.loading) {
                  this.setState({ loading: false, deleting: false });
                  this.props.deletedTag(tag);
                }

                return (
                  <div
                    className={delete_tag}
                    onClick={() => {
                      if (
                        window.confirm(
                          tag.tag_type
                            ? "Are you sure you want to delete this tag group?."
                            : "Are you sure you want to delete this tag and remove it from all items it's been added to? This action cannot be undone."
                        )
                      ) {
                        this.setState({ loading: true, deleting: true });
                        let variables = { id: tag.id };
                        mutate({ variables });
                      } else {
                        /* Do nothing */
                      }
                    }}
                  >
                    {tag.tag_type
                      ? "Delete tag group"
                      : "Delete tag permanently"}
                  </div>
                );
              }}
            </Mutation>

            <div className={delete_tag_warning}>
              {tag.tag_type
                ? 'No tags will be deleted. All tags within this group will be transfered to the "ungrouped section"'
                : "This will permanently remove tag from all items you've added it to previously."}
            </div>
          </div>

          {this.state.deleting && <GhostLoader />}
        </div>
      </div>
    );
  }
}

// *************************
// * ADD NEW TAG COMPONENT *
// *************************
class AddEmail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: this.props.value || ""
    };
  }

  isValidEmail() {
    return validateEmail(this.state.value);
  }

  render() {
    let { group, setVal } = this.props;

    return (
      <div className={tag_input}>
        <label>
          <input
            type="text"
            value={this.state.value}
            onChange={e =>
              this.setState({ value: e.target.value.toLowerCase() })
            }
            placeholder="Add email"
          />

          {this.isValidEmail() && (
            <i
              style={{
                visibility: this.state.value === "" ? "hidden" : "visible"
              }}
              className="fas fa-plus-circle"
              onClick={() => {
                group.data.emails.push(this.state.value);
                this.setState({ value: "" });
                setVal(group);
              }}
            />
          )}
        </label>
      </div>
    );
  }
}

// **********************************
// * CREATE NEW TAG GROUP COMPONENT *
// **********************************
class CreateNewGroup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: "",
      submitting: false,
      show: true
    };
  }

  render() {
    return (
      <Mutation mutation={createTag}>
        {(mutate, { data, loading, error }) => {
          if (!loading && !error && data && this.state.submitting) {
            this.setState({ submitting: false, value: "" });
            this.props.setVal(data.createTag);
          }
          return (
            <div>
              <div
                onClick={() => {
                  this.setState({
                    show: true
                  });
                }}
              >
                Create new tag group
              </div>
              {this.state.show && (
                <div className={tag_input}>
                  <label>
                    <input
                      type="text"
                      value={this.state.value}
                      placeholder="Group name"
                      onChange={e => this.setState({ value: e.target.value })}
                    />

                    {!this.state.submitting && (
                      <i
                        style={{
                          visibility:
                            this.state.value === "" ? "hidden" : "visible"
                        }}
                        className="fas fa-plus-circle"
                        onClick={() => {
                          this.setState({ submitting: true });
                          let variables = {
                            input: {
                              name: this.state.value,
                              tag_type: "SHARING",
                              index: this.props.index
                            }
                          };
                          mutate({ variables });
                          this.setState({ submitting: true });
                        }}
                      />
                    )}

                    {this.state.submitting && (
                      <i className="fas fa-spinner fa-spin" />
                    )}
                  </label>
                </div>
              )}
            </div>
          );
        }}
      </Mutation>
    );
  }
}

// ******************
// * ADD EVALUATION *
// ******************
class AddEvaluation extends Component {
  constructor(props) {
    super(props);
    this.state = { value: "" };
  }

  getFilteredEvaluations() {
    if (this.state.value === "") return [];
    let arr = (this.props.data || []).filter(d =>
      d.organization.name.toLowerCase().includes(this.state.value.toLowerCase())
    );

    arr = arr.filter(d => !this.props.evaluations.some(e => e.id === d.id));

    if (arr.length > 10) arr.length = 10;
    return arr;
  }

  render() {
    let { setVal, evaluations } = this.props;

    return (
      <div>
        <div className={tag_input}>
          <label>
            <input
              type="text"
              value={this.state.value}
              onChange={e => this.setState({ value: e.target.value })}
              placeholder="Add evaluation"
            />
          </label>
        </div>

        <div style={{ margin: "10px -2px" }}>
          {this.getFilteredEvaluations().map((d, i) => (
            <div key={`i-${i}`} className={tag_class}>
              {d.organization.name}
              <div className={tag_edit_button}>
                <i
                  className="fas fa-plus-circle"
                  onClick={() => {
                    let setData = {
                      name: d.organization.name,
                      id: d.id
                    };
                    setVal(setData);
                    this.setState({ value: "" });
                  }}
                />
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

// **************
// * GET GROUPS *
// **************
const getGroups = data => {
  return data
    .filter(d => d.tag_type === "SHARING")
    .sort((a, b) => a.index - b.index)
    .map(s => {
      let data = {
        emails: [],
        evaluations: []
      };
      try {
        let d = JSON.parse(s.data);
        data.emails = d.emails || data.emails || [];
        data.evaluations = d.evaluations || data.evaluations || [];
      } catch (err) {
        /* do nothing */
      }
      return { ...s, data };
    });
};

class Comp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      groups: getGroups(this.props.data),
      showModal: false,
      modalContent: null
    };
  }

  render() {
    console.log("this.state", this.state);

    return (
      <div>
        {/* GROUPS */}
        {this.state.groups.map(group => {
          let { emails, evaluations } = group.data;
          return (
            <div className={tag_section} key={`group-${group.id}`}>
              <div className={tag_section_title}>
                <span>{group.name}</span>
                <span
                  className={tag_group_edit}
                  onClick={() => {
                    this.setState({
                      showModal: true,
                      modalContent: group
                    });
                  }}
                >
                  <i className="far fa-edit" />
                </span>
              </div>

              {/* EMAIL SECTION */}
              <div>
                <div className={group_section}>
                  {emails.length !== 0 && (
                    <div className={section_header}>Emails</div>
                  )}

                  {emails.map((email, i) => {
                    return (
                      <div key={`${group.id}-${email}`} className={tag_class}>
                        <span>{email}</span>
                        <div
                          className={tag_edit_button}
                          onClick={() => {
                            let newGroupData = {
                              emails: emails.filter(mail => mail !== email),
                              evaluations
                            };

                            let newGroup = {
                              ...omit(group, [
                                "createdBy",
                                "createdAt",
                                "updatedAt",
                                "__typename"
                              ]),
                              data: JSON.stringify(newGroupData)
                            };
                            let variables = { input: [newGroup] };
                            this.props.mutate({ variables });

                            // Shitty optimistic: todo: redo
                            this.setState({
                              groups: this.state.groups.map(g => {
                                return g.id === group.id
                                  ? { ...group, data: newGroupData }
                                  : g;
                              })
                            });
                          }}
                        >
                          <i className="fas fa-minus-circle" />
                        </div>
                      </div>
                    );
                  })}
                </div>

                <AddEmail
                  group={group}
                  setVal={group => {
                    let variables = {
                      input: [
                        {
                          ...omit(group, [
                            "createdBy",
                            "createdAt",
                            "updatedAt",
                            "__typename"
                          ]),
                          data: JSON.stringify(group.data)
                        }
                      ]
                    };
                    this.props.mutate({ variables });
                  }}
                />
              </div>

              {/* EVALUATION SECTION */}
              <div>
                <div style={{ marginTop: "20px" }}>
                  {evaluations.length !== 0 && (
                    <div className={section_header}>evaluations</div>
                  )}

                  {evaluations.map((evaluation, i) => {
                    return (
                      <div key={`${evaluation.id}-${i}`} className={tag_class}>
                        <span>{evaluation.name}</span>
                        <div
                          className={tag_edit_button}
                          onClick={() => {
                            let newGroupData = {
                              emails,
                              evaluations: evaluations.filter(
                                _evalutaion => _evalutaion.id !== evaluation.id
                              )
                            };

                            let newGroup = {
                              ...omit(group, [
                                "createdBy",
                                "createdAt",
                                "updatedAt",
                                "__typename"
                              ]),
                              data: JSON.stringify(newGroupData)
                            };

                            let variables = { input: [newGroup] };

                            // return console.log('variables', variables)

                            this.props.mutate({ variables });

                            // Shitty optimistic: todo: redo
                            this.setState({
                              groups: this.state.groups.map(g => {
                                return g.id === group.id
                                  ? { ...group, data: newGroupData }
                                  : g;
                              })
                            });
                          }}
                        >
                          <i className="fas fa-minus-circle" />
                        </div>
                      </div>
                    );
                  })}
                </div>

                <AddEvaluation
                  data={this.props.evaluations}
                  evaluations={evaluations}
                  setVal={newData => {
                    group.data.evaluations.push(newData);
                    let variables = {
                      input: [
                        {
                          ...omit(group, [
                            "createdBy",
                            "createdAt",
                            "updatedAt",
                            "__typename"
                          ]),
                          data: JSON.stringify(group.data)
                        }
                      ]
                    };
                    this.props.mutate({ variables });
                  }}
                />
              </div>
            </div>
          );
        })}

        {/* CREATE NEW GROUP */}
        <CreateNewGroup
          index={this.state.groups.length}
          setVal={newGroup => {
            this.setState({
              groups: [
                ...this.state.groups,
                {
                  ...newGroup,
                  data: {
                    emails: [],
                    evaluations: []
                  }
                }
              ]
            });
          }}
        />

        <Modal
          tag={this.state.modalContent}
          show={this.state.showModal}
          close={() => {
            this.setState({ showModal: false });
          }}
          updatedTag={updatedTag => {
            let data = { emails: [], evaluations: [] };
            try {
              data = JSON.parse(updatedTag.data);
            } catch (error) {
              /* DO NOTHING */
            }
            this.setState(
              {
                groups: this.state.groups.map(g =>
                  g.id !== updatedTag.id ? g : { ...updatedTag, data }
                )
              },
              () => this.setState({ modalContent: updatedTag })
            );
          }}
          deletedTag={deletedTag => {
            this.setState({
              groups: [
                ...this.state.groups.filter(g => g.id !== deletedTag.id)
              ],
              showModal: false
            });
          }}
        />
      </div>
    );
  }
}

