import React from 'react';
import API from "@aws-amplify/api";
import qp from '../../../utils/queryParams';


class Comp extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      loadingGoogleCallback: false
    }
  }

  componentDidMount() {
    let { code } = qp(this.props.location.search);

    console.log('code', code)

    if (code && !this.state.loadingGoogleCallback) {
      this.setState({ loadingGoogleCallback: true });



      console.log('code[0]', code[0])

      API.get("GoogleAuth", `/dev/oauth2Callback?code=${code[0]}`)
        .then((resp) => {
          console.log('success', resp)
          window.location.href = window.location.href.split("?")[0];
        })
        .catch((err) => {
          console.log('error', err)
          window.location.href = window.location.href.split("?")[0];
        });
    }
  }

  render() {
    return (
      <div>
        <div
          onClick={() => {
            let apiName = "GoogleAuth";
            let path = "/dev/authUrl";
            API.get(apiName, path).then((resp) => (window.location = resp.url));
          }}
        >
          DO IT!
        </div>
      </div>

    )
  }

}


export default Comp;

